<template>
  <div>
    <canvas
      id="canvas"
      :width=300
      :height=300
      ref="canvas"
    >抱歉，您的浏览器不支持canvas元素</canvas>
  </div>
</template>

<script>
import AlloyFinger from 'alloyfinger'
export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      height: document.body.clientHeight,
      width: document.body.clientWidth,
      initScale: 1,
      ctx: {},
      canvas: {},
      zoom: 1
    }
  },
  mounted () {
    var af = new AlloyFinger(this.$refs.canvas, {
      touchStart: function (e) {},
      touchMove: function (e) {},
      touchEnd: function () { console.log(this.zoom)},
      touchCancel: function () { },
      multipointStart: function () { },
      multipointEnd: function () { },
      tap: function () { },
      doubleTap: function () { console.log('doubletap') },
      longTap: function () { console.log('longTap') },
      singleTap: function () { },
      rotate: function (evt) {
        console.log(evt.angle)
      },
      // pinch: function (evt) {},
      pressMove: function (evt) {
        console.log(evt.deltaX)
        console.log(evt.deltaY)
      },
      swipe: function (evt) {
        console.log('swipe' + evt.direction)
      }
    })
    af.on('tap', this.tap)
    af.on('touchStart', this.touchStart)
    af.on('touchMove', this.touchMove)
    af.on('pinch', this.pinch)
    // af.on('pinch', this.pinch())
    this.canvas = this.$refs.canvas// 指定canvas
    this.ctx = this.canvas.getContext('2d')// 设置2D渲染区域
    this.ctx.lineWidth = 5// 设置线的宽度
    // this.canvas.style = 'transform:translate(50px,40px)'
    // this.ctx.style = 'translate(' + this.width / 2 + ',' + this.height / 2 + ')'
    // const that = this
    // window.onresize = () => {
    //   return (() => {
    //     // window.screenWidth = document.body.clientWidth
    //     // window.screenHeight = document.body.clientHeight
    //     that.width = document.body.clientWidth
    //     that.height = document.body.clientHeight
    //   })()
    // }
  },
  methods: {
    // 防抖
    debounce: function (fn, wait) {
      var timeout = null
      return function () {
        if (timeout !== null) clearTimeout(timeout)
        timeout = setTimeout(fn, wait)
      }
    },
    touchStart: function (e) {
      e.stopPropagation()
      e.preventDefault()
      if (e.touches.length === 1) {
        console.log('start')
        this.ctx.beginPath()
        this.ctx.moveTo(e.targetTouches[0].pageX, e.targetTouches[0].pageY)
        console.log('start')
        console.log(e)
        // this.ctx.lineTo(e.targetTouches[0].pageX, e.targetTouches[0].pageY)
        this.ctx.strockStyle = '#333'
        this.ctx.stroke()
      }
    },
    touchMove: function (e) {
      e.stopPropagation()
      e.preventDefault()
      // console.log(e)
      if (e.touches.length === 1) {
        this.ctx.lineTo(e.targetTouches[0].pageX, e.targetTouches[0].pageY)
        this.ctx.strockStyle = '#333'
        this.ctx.stroke()
        // if (e.touches.length >= 2) {
        //   this.canvas.scaleX = this.canvas.scaleY = this.initScale * e.zoom
        // }
      }
    },
    // touchEnd: function (e) {
    //   console.log(e)
    //   // this.ctx.lineTo(e.targetTouches[0].pageX, e.targetTouches[0].pageY)
    //   console.log('end')
    // },
    tap: function (e) {
      e.stopPropagation()
      e.preventDefault()
      // this.canvas.style = 'transform:translate(-' + e.changedTouches[0].pageX + 'px,-' + e.changedTouches[0].pageY + 'px)'
      // console.log(e.changedTouches[0].pageX)
    },
    pinch: function (e) {
      // alert(e)
      e.stopPropagation()
      // eslint-disable-next-line func-call-spacing
      e.preventDefault()
      // eslint-disable-next-line no-unexpected-multiline
      this.zoom *= e.zoom
      console.log(this.zoom)
      console.log(e)
      console.log(this.canvas)
      // document.getElementsByTagName('body')[0].style.zoom = e.zoom
      // this.canvas.style = 'transform:translate(' + (e.changedTouches[0].screenX + e.changedTouches[1].screenX) / 2 + 'px,' + (e.changedTouches[0].screenY + e.changedTouches[1].screenY) / 2 + 'px)'
      this.canvas.style = 'transform:scale(' + e.zoom + ',' + e.zoom + ')'
      // this.canvas.style = 'transform:translate(-' + (e.changedTouches[0].screenX + e.changedTouches[1].screenX) / 2 + 'px,-' + (e.changedTouches[0].screenY + e.changedTouches[1].screenY) / 2 + 'px)'
    }
  },
  watch: {
  }
}
</script>

<style>
  #canvas{
    background: #FEFDC5;
  }
  html,body{
    padding:0;
    margin: 0;
    height: 100%;
  }
</style>



